import React, {Component} from 'react';
import CardList from './CardList';
import {robots} from './robots';
import SearchBox from './SearchBox';
import Scroll from './Scroll';
import './App.css';


class App extends Component {
    constructor() {
        super()
        this.state = {
            robots: robots,
            searchfield: '' 
        }
    }

    onSearchChange = (event) => {
            this.setState({searchfield: event.target.value})
            }

    render () {
        const filteredRobots = this.state.robots.filter(robots =>{
        return robots.name.toLowerCase().includes(this.state.searchfield.toLowerCase());
        })
        return (
        <div>
            <div className='naslov'>
                <h1>WARFRAME</h1>
                <p className='naslovM'>-svi frejmovi-</p>
                <SearchBox searchChange={this.onSearchChange}/>
            </div>
        <Scroll>
        <CardList robots={filteredRobots}/>
        </Scroll>
        <p className='autor'>This app is made for React learning purpose! All images are copyright to their respective owners: Warframe.com</p>
        <p className='autor'>Made by: SalWe {`{HTML => programmer}`}</p>
        </div>
    );
    }
    }
    

export default App;