import React from 'react';
import './Card.css'

const Card = ({image, name}) => {
     return (
        <div className='allCards'>
        <div className='Card'>
            <img alt='frames' src={image} />
            <div>
                <h2>{name}</h2>
            </div>
        </div>
        </div>
    )
}

export default Card;